import React, { useState, useEffect, useCallback } from 'react'
import AppConnect from './App.connect'
import { LoginScreen, config, googleAnalytics, LocalForageCache } from '@riverscapes/react-common'
import { Route, Switch, History, useHistory } from 'react-router-dom'
import { Loading } from '@northarrowresearch/react-common'
import { useHosted, UseHostedReturn } from '@northarrowresearch/react-auth'
import { warehouseIconUrl, appDefaults, appRouterPath } from '../config'
import { authCheck } from '@riverscapes/common'
import AppWrapper from '../components/AppWrapper'
import ProjectNotFound from '../components/ProjectNotFound'
import { Program, Project, getProgramAuth } from '@riverscapes/common'
import log from 'loglevel'

googleAnalytics.init()

const LOCAL_CACHE = new LocalForageCache('AppAuth', 5 * 60) // We only need 5 minutes tops on this one
LOCAL_CACHE.prune() // prune once on load

const getProgramMachineName = (): string | null => {
    // If we just get the program from the hash it makes a lot less API calls
    const locationHash = window.location.hash.split('/')
    return locationHash.length > 1 && locationHash[1].match(/[A-Za-z0-9-_]{3,25}/g) ? locationHash[1] : null
}

const AppAuthConnect: React.FC = () => {
    const [authInfo, setAuthInfo] = useState<Record<string, unknown>>()
    const [programState, setProgramState] = useState<{
        program: Program | null
        loading: boolean
        machineName: string | null
    }>({
        program: null,
        loading: true,
        machineName: getProgramMachineName()
    })
    const [isAppPreLoading, setIsAppPreLoading] = useState(true)
    const auth: UseHostedReturn = useHosted(authInfo as Record<string, unknown>, false, false) //config.isDev
    const history = useHistory()

    // Go get all the startup info we need to start logging in
    // Initial fetch gets us all the program objects
    useEffect(() => {
        // Register the current page view
        googleAnalytics.currentPageView()

        authCheck(config.endpoints.graphql as string, '', appDefaults.apiTimeout)
            .then((authResp) => {
                log.debug('authResp', authResp)
                const authVals = authResp.data?.auth
                if (authVals && authVals.region) {
                    setAuthInfo({
                        region: authVals.region,
                        userPoolId: authVals.userPool,
                        userPoolWebClientId: authVals.clientId,
                        mandatorySignIn: true,
                        oauth: {
                            domain: authVals.domain,
                            scope: ['email', 'profile', 'openid'],
                            redirectSignIn: config.redirectUrl.toString(),
                            redirectSignOut: config.redirectUrl.toString(),
                            responseType: 'code'
                        }
                    })
                    setIsAppPreLoading(false)
                }
            })
            .catch((err) => {
                log.error('api.getPrograms error', err)
            })
    }, [])

    // This lets us do a redirect after login using localstorage
    useEffect(() => {
        if (!history) return

        const check = async () => {
            const cacheKey = `RSUrl`
            const storageUrl = await LOCAL_CACHE.get<string>(cacheKey)

            const localUrl = history.location.pathname
            log.debug(`[${cacheKey}] Cache:::REDIRECT checking ${storageUrl} === ${localUrl}`)

            if (!auth.isAuthenticated && !auth.error && auth.isAuthComplete) {
                log.debug(`[${cacheKey}] Cache:::REDIRECT:::SET ${localUrl}`)
                LOCAL_CACHE.set(cacheKey, localUrl)
            }
            if (storageUrl && auth.isAuthenticated && auth.isAuthComplete && !auth.error) {
                if (localUrl !== storageUrl) {
                    log.debug(`[${cacheKey}] Cache:::REDIRECT Needed! ${localUrl} !== ${storageUrl}`)
                    history.push(storageUrl)
                    // Now we need to trigger a re-fetch of the program
                    if (!programState.machineName) {
                        const machineName = getProgramMachineName()
                        log.debug('Setting program machine name', machineName)
                        setProgramState({ ...programState, machineName })
                    }
                }
                // Delete once we're done to stop strange behaviour later
                LOCAL_CACHE.del(cacheKey)
            }
        }
        check()
    }, [history, auth])

    const navigate = useCallback((history: History, program: Program, project?: Project): void => {
        let address

        if (project) address = '/' + program.machineName + '/' + project.id
        else address = '/' + program.machineName

        googleAnalytics.event('webrave', 'navigate', address)
        history.push(address)
    }, [])

    // Initial fetch gets us all the program objects
    useEffect(() => {
        if (!auth.isAuthenticated || programState?.program || !programState.machineName) return

        auth.getCurrentToken()
            .then((jwt) =>
                getProgramAuth(config.endpoints.graphql as string, jwt, programState.machineName || 'UNKNOWN')
            )
            .then((apiReturn) => {
                log.debug('API GetProgram', apiReturn)
                if (apiReturn.data && apiReturn.data.program) {
                    setProgramState({
                        program: apiReturn.data.program,
                        loading: false,
                        machineName: apiReturn.data.program.machineName
                    })
                }
            })
            .catch((err) => {
                log.error('api.getPrograms error', err)
                // setIsAppPreLoading(false)
            })
    }, [auth, programState])

    const logoSVG = <img src={warehouseIconUrl} alt="logo" />

    // render the login helper screen if that's called for
    if (isAppPreLoading) {
        return (
            <AppWrapper title="Loading authentication info">
                <Loading
                    logoSVG={logoSVG}
                    title={appDefaults.appName}
                    message={'Loading authentication info'}
                    version={config.version}
                />
            </AppWrapper>
        )
    } else {
        if (!auth.isAuthenticated) {
            if (!auth.isAuthComplete) {
                return (
                    <AppWrapper title="webRAVE">
                        <Loading
                            logoSVG={logoSVG}
                            title="Authenticating"
                            message={'Authenticating'}
                            version={config.version}
                        />
                    </AppWrapper>
                )
            }
            return (
                <AppWrapper title="Sign in">
                    <LoginScreen signIn={auth.signIn} title={'Riverscapes Map Viewer'} />
                </AppWrapper>
            )
        } else if (!programState || programState?.loading) {
            return (
                <AppWrapper title="webRAVE">
                    <Loading
                        logoSVG={logoSVG}
                        title={'WebRave'}
                        message={`Loading Program: ${programState.machineName || 'NULL'}`}
                        version={config.version}
                    />
                </AppWrapper>
            )
        } else {
            const appWrapProps = { navigate, signOut: auth.signOut }
            return (
                <Switch>
                    <Route
                        path={appRouterPath}
                        render={(props): React.ReactElement => (
                            <AppConnect {...props} auth={auth} program={programState?.program} />
                        )}
                    />
                    <Route
                        path="*"
                        render={(props): React.ReactElement => (
                            <AppWrapper {...props} {...appWrapProps}>
                                <ProjectNotFound />
                            </AppWrapper>
                        )}
                    />
                </Switch>
            )
        }
    }
}
export default AppAuthConnect
