import React from 'react'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import { BaseLayer } from '../../types'

interface BaseLayerPickerProps {
    baseLayer: BaseLayer
    baseLayers: BaseLayer[]
    setBaseLayer: (baseLyr: BaseLayer) => void
}

const useStyles = makeStyles((theme) => ({
    root: {
        // margin: theme.spacing(1, 0)
    },
    menuIcon: {
        marginRight: theme.spacing(1)
    },
    menuItem: {
        display: 'flex'
    }
}))

const BaseLayerPicker: React.FC<BaseLayerPickerProps> = ({
    baseLayer,
    baseLayers,
    setBaseLayer
}: BaseLayerPickerProps) => {
    const classes = useStyles()

    const viewOptions = baseLayers.map((v, idx) => {
        const Icon = v.Icon
        return (
            <MenuItem key={`view-${idx}`} value={v.id} className={classes.menuItem}>
                <Icon className={classes.menuIcon} />
                <Typography>{v.title}</Typography>
            </MenuItem>
        )
    })

    return (
        <FormControl className={classes.root} fullWidth size="small">
            <InputLabel>Basemap</InputLabel>
            <Select
                id="baselayer"
                value={baseLayer.id}
                variant="filled"
                classes={{
                    selectMenu: classes.menuItem
                }}
                onChange={(event) => {
                    const newBl = baseLayers.find((bl) => bl.id === event.target.value)
                    if (newBl) setBaseLayer(newBl)
                }}
            >
                {viewOptions}
            </Select>
        </FormControl>
    )
}

export default BaseLayerPicker
