import { selector, selectorFamily } from 'recoil'
import { endpoints, appDefaults } from '../config'
// import { orange } from '@material-ui/core/colors'
import {
    Symbology,
    SymbologyStatus,
    getWebSymbology,
    ProjectTreeLayerTypes,
    ProjectTreeLeaf
} from '@riverscapes/common'
import { LocalForageCache } from '@riverscapes/react-common'
import downloadQueue from '../lib/downloadQueue'
import { authAtom, projectAtom, activeTreeLeavesSelector, projectMapLayerLeafsSelector } from './project'
import log from 'loglevel'
import { isEqual } from 'lodash'

const LOCAL_CACHE = new LocalForageCache('SYMB', 6 * 60 * 60) // 6 hour ttl since symbologies almost never change
LOCAL_CACHE.prune() // prune once on load

/****************************************
 * Tile Vector Symbology Library
 ****************************************/
/**
 * Selector for all the unique symbologies currently on the map
 */
export type SymbologyKey = [string, boolean]

const createSymbologyKeys = (leaves: ProjectTreeLeaf[]): SymbologyKey[] => {
    return leaves.reduce<SymbologyKey[]>((acc, leaf) => {
        if (!leaf.symbology) return acc
        const symbKey: SymbologyKey = [leaf.symbology, leaf.layerType === ProjectTreeLayerTypes.RASTER]
        if (!acc.find((sk) => isEqual(sk, symbKey))) return [...acc, symbKey]
        else return acc
    }, [])
}

/**
 * Selector for all the unique symbologies in the whole project
 * Use to initilize project-level downloader
 */
export const projectSymbologiesSelector = selector<SymbologyKey[]>({
    key: 'symbology/uniqueSymb',
    get: ({ get }) => {
        const mapLyrs = get(projectMapLayerLeafsSelector)
        return createSymbologyKeys(mapLyrs)
    }
})

// A unique symbology key consists of a string name and whether or not it is a raster
export const activeMapSymbologiesSelector = selector<SymbologyKey[]>({
    key: 'symbology/activeSymbologies',
    get: ({ get }) => {
        const activeSymb = get(activeTreeLeavesSelector)
        return createSymbologyKeys(activeSymb)
    }
})

export const symbologySelectorFamily = selectorFamily<Symbology | null, SymbologyKey>({
    key: 'symbology/symbology',
    get:
        ([name, isRaster]) =>
        async ({ get }) => {
            const proj = get(projectAtom)
            const auth = get(authAtom)
            if (!proj || !auth || !name) return null
            const retVal: Symbology = {
                name,
                status: SymbologyStatus.UNKNOWN
            }

            // Localstorage cache check
            const cacheKey = `${proj.projType}/${name}`
            const cacheVal = await LOCAL_CACHE.get<Symbology>(cacheKey)

            if (cacheVal) return cacheVal

            // Make the Asynchronous call to the API here
            // A priority between 0 and 9. A job with a priority of 4 will be queued ahead of a job with a priority of 5
            // Default is 5
            const apiRes = await downloadQueue.schedule({ priority: 5 }, () =>
                auth
                    .getCurrentToken()
                    .then((jwt) =>
                        getWebSymbology(endpoints.graphql, jwt, proj.projType, name, isRaster, appDefaults.apiTimeout)
                    )
                    .catch((err) => {
                        log.debug('symbologySelectorFamily', err)
                    })
            )

            if (!apiRes || apiRes.errors) {
                retVal.status = SymbologyStatus.ERROR
                retVal.error = 'Error downloading symbology'
                return retVal
            }

            const finalVal = apiRes.data?.getWebSymbology
                ? {
                      ...apiRes.data?.getWebSymbology,
                      status: SymbologyStatus.FOUND
                  }
                : retVal
            if (finalVal.status === SymbologyStatus.FOUND) await LOCAL_CACHE.set<Symbology>(cacheKey, finalVal)
            return finalVal
        }
})
