import React from 'react'
import { dates } from '@northarrowresearch/common'
import { DownloadZipBtn, PendingZips } from '@riverscapes/react-common'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider'
import { warehouseUrl } from '../config'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
import { Project } from '@riverscapes/common'
import MetaData from './meta/MetaData'
import { WarehouseButton } from '@riverscapes/react-common'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1),
        height: '100%',
        overflowY: 'scroll',
        userSelect: 'text',
        '& *': {
            userSelect: 'text'
        }
    },
    btnGroup: {
        width: '100%',
        display: 'flex',
        justifyContent: 'space-between',
        '&>*': {
            flex: '0 0 calc(50% - 0.5em)'
        },
        margin: theme.spacing(1, 1, 3, 1)
    },
    divider: {
        marginTop: theme.spacing(3)
    },
    footerLogo: {}
}))

interface ProjectInfoTabProps {
    project: Project
    downloadZipFile: (project: Project) => Promise<void>
    pendingZips: PendingZips
}

const ProjectInfoTab: React.FC<ProjectInfoTabProps> = ({
    project,
    downloadZipFile,
    pendingZips
}: ProjectInfoTabProps) => {
    const classes = useStyles()

    const projectInfo = {
        Title: project.name,
        Type: project.projType,
        'Warehouse ID': project.id,
        'Warehouse Tags': Array.isArray(project.tags) ? project.tags.join(', ') : '<None>',
        'Project Created': `${dates.formatDate(project.createdOn, 'lll')} (${dates.formatDateAgo(project.createdOn)})`,
        'Project Updated': `${dates.formatDate(project.updatedOn, 'lll')} (${dates.formatDateAgo(project.updatedOn)})`
    }
    const pendingSince = project && pendingZips[project.id]
    return (
        <Paper className={classes.root}>
            <Typography variant="h4" paragraph color="textPrimary">
                {project.projType} Project
            </Typography>
            <Divider />
            <Typography component="div" paragraph>
                This project exists within the Riverscapes Data Warehouse. You can download this project and view it
                using the free{' '}
                <a href="https://rave.riverscapes.xyz" target="_blank" rel="noreferrer">
                    RAVE
                </a>{' '}
                {/* plugin for either QGIS or ArcGIS. */}
                plugin for ArcGIS.
            </Typography>
            <div className={classes.btnGroup}>
                <WarehouseButton title={`Warehouse`} href={`${warehouseUrl}/#/${project.program}/${project.id}`} />
                <DownloadZipBtn
                    downloadZipFile={downloadZipFile}
                    pendingSince={pendingSince}
                    project={project}
                    // btnProps={{}}
                    title="Download"
                />
            </div>
            <MetaData title="Project Information" meta={projectInfo} />
            <MetaData title="Project Metadata" meta={project.meta} />

            <Divider className={classes.divider} />
            <a href="http://riverscapes.xyz" target="_blank" rel="noreferrer">
                <img
                    className={classes.footerLogo}
                    alt="footer"
                    src={`${process.env.PUBLIC_URL}/cc-watermarks-riverscapes_orig.png`}
                />
            </a>
        </Paper>
    )
}

export default ProjectInfoTab
