import React from 'react'
import { History, useHistory } from 'react-router-dom'
import log from 'loglevel'

import { ThemeProvider } from '@material-ui/styles'
import { AppFrame } from '@northarrowresearch/react-common'
import { ContainerProps } from '@material-ui/core/Container'

import { Topbar, themes } from '@riverscapes/react-common'

interface Props {
    title: string
    children: React.ReactChild | React.ReactChildren | React.ReactElement
    history?: History
    containerProps?: Partial<ContainerProps>
    signOut?: () => Promise<void>
}

/**
 * Note: We keep this as a separate component in case WebRAVE needs a second screen
 * (like a settings screen or something)
 * It's basically the screen template with topbar and app frame all set up
 * @param param0
 * @returns
 */
const AppWrapper: React.FC<Props> = ({ title, children, history, signOut, containerProps }: Props) => {
    return (
        <ThemeProvider theme={themes.home}>
            <AppFrame
                containerProps={{ maxWidth: 'md', ...containerProps }}
                noScroll
                fullWidth
                topbar={
                    <Topbar history={history} title={title} signOutHandler={signOut} shareUrl={window.location.href} />
                }
                // footer={<Footer version={config.version as string} />}
            >
                <>{children}</>
            </AppFrame>
        </ThemeProvider>
    )
}
export default AppWrapper

/**
 * The cosmos wrapper is here to make our cosmos experience prettier
 * @param param0
 * @returns
 */
export const CosmosAppWrapper: React.FC<Props> = ({ children, containerProps }: Props) => {
    const signOut = (): Promise<void> => Promise.resolve(log.info('logging out'))
    const history = useHistory()
    return (
        <ThemeProvider theme={themes.home}>
            <AppFrame
                containerProps={{ maxWidth: 'md', ...containerProps }}
                noScroll
                fullWidth
                topbar={
                    <Topbar
                        history={history}
                        title="Riverscapes Warehouse"
                        signOutHandler={signOut}
                        shareUrl={window.location.href}
                    />
                }
            >
                {children}
            </AppFrame>
        </ThemeProvider>
    )
}
