import React, { useState, useRef, useEffect, useCallback, useMemo } from 'react'
import { config } from '@riverscapes/react-common'
import { useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil'
import MapGL, { ViewportProps, NavigationControl } from 'react-map-gl'
import { makeStyles } from '@material-ui/core/styles'
import { CircularProgress } from '@material-ui/core'
import MapLoading from './MapLoading'
import { geoFence } from '../../lib/util'
import { createInitialStyle } from '../../lib/MapManager'
import { appDefaults } from '../../config'
import { viewportAtom, mapLoadedAtom, boundsAtom, mapSizeAtom } from '../../recoil/map'
import { BaseLayer } from '../../types'

import 'mapbox-gl/dist/mapbox-gl.css'

import _ from 'lodash'
import log from 'loglevel'

interface MapProps {
    baseLayer: BaseLayer
    styleLoading?: boolean
    bounds?: [minx: number, miny: number, maxx: number, maxy: number]
    zoomBounds: [minZoom: number, maxZoom: number]
    children?: React.ReactChild | React.ReactChildren | React.ReactElement[]
}

const navControlStyle = {
    left: 15,
    top: 60
}

const useStyles = makeStyles((theme) => ({
    '@global': {
        '.mapboxgl-ctrl-bottom-left': {
            zIndex: '0 !important', // This is 1000 by default
            position: 'absolute',
            pointerEvents: 'none',
            left: 'initial',
            right: theme.spacing(2),
            bottom: theme.spacing(3)
        }
    },
    root: {
        marginLeft: theme.spacing(1)
    },
    container: {
        display: 'flex',
        height: '100%'
    },
    mapContainer: {
        position: 'relative',
        background: '#CCCCCC',
        height: '100%',
        width: '100%',
        '&>h1': {
            color: '#9999FF',
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)'
        }
    },
    drawerContents: {
        // border: '1px solid green',
        height: '100%',
        display: 'flex'
    },
    drawerSpacer: {
        flex: '0 0 0%'
    },
    drawerSpacerOpen: {
        // flex: `0 0 ${DRAWER_WIDTH}`
    },
    dialogContainer: {
        alignItems: 'left',
        justifyContent: 'left'
    },
    loaderSpinner: {
        position: 'absolute',
        bottom: 10,
        right: 10,
        color: theme.palette.secondary.main
    },
    dialog: {
        position: 'absolute',
        margin: 0,
        left: '20px',
        top: '20px'
    },
    subheaders: {
        background: 'white'
    },
    dummyText: {}
}))

const debounceConsoleLog = _.debounce((...vals) => log.debug(...vals), 1500)

const Map: React.FC<MapProps> = ({ children, baseLayer, styleLoading, bounds, zoomBounds }: MapProps) => {
    const classes = useStyles()
    const [viewport, _setViewport] = useRecoilState(viewportAtom)
    const [boundsSet, setBoundsSet] = useState<boolean>(false)
    const rawBounds = useRecoilValue(boundsAtom)
    const setMapSize = useSetRecoilState(mapSizeAtom)
    const [mapLoaded, setMapLoaded] = useRecoilState(mapLoadedAtom)

    const viewportRef = useRef<ViewportProps>()
    viewportRef.current = viewport

    const loadHandler = useCallback(
        (e) => {
            const styleLoaded = e.target.isStyleLoaded()
            if (mapLoaded !== styleLoaded) setMapLoaded(styleLoaded)
        },
        [mapLoaded]
    )

    const handleResize = ({ width, height }) => {
        console.log('resize map', width, height)
        setMapSize(width > 0 && height > 0 ? { width, height } : null)
    }

    // Constrain the Bounds and zoom
    const setViewport = useCallback(
        (inV: ViewportProps) => {
            const outV = { ...viewportRef.current, ...inV }
            // Constrict bounds
            if (zoomBounds && outV.zoom) {
                if (outV.zoom > zoomBounds[1]) outV.zoom = zoomBounds[1]
                else if (outV.zoom < zoomBounds[0]) outV.zoom = zoomBounds[0]
            }
            const geoFenceCorrect = geoFence(
                [outV.longitude, outV.latitude] as [number, number],
                // Bounds is an object or fall back to the world fence
                bounds || appDefaults.worldBounds,
                // We center the world if we need to
                Boolean(rawBounds && !boundsSet)
            )

            // If we're out of bounds then bring us back
            if (Array.isArray(geoFenceCorrect)) {
                outV.longitude = geoFenceCorrect[0]
                outV.latitude = geoFenceCorrect[1]
            }

            if (rawBounds && !boundsSet) setBoundsSet(true)

            debounceConsoleLog('setViewport DEBOUNCED', { inV, outV, bounds, center: bounds && !boundsSet })
            _setViewport(outV)
        },
        [bounds, zoomBounds, boundsSet, rawBounds]
    )

    // Just the first time we offer a default style
    const baseStyle = useMemo(() => createInitialStyle(baseLayer), [])

    // When loading we may get an initial zoom and/or bounds value
    useEffect(() => {
        debounceConsoleLog('setViewport useEffect', { bounds, zoomBounds, boundsSet, rawBounds })
        setViewport({ ...viewportRef.current })
    }, [bounds, zoomBounds, boundsSet, rawBounds])

    const mapLoader = styleLoading && <CircularProgress size="2rem" thickness={6} className={classes.loaderSpinner} />

    const onError = (e) => {
        // Hide those annoying non-error errors. Cloudfront hides 404 errors behind 403
        if (!e.error || !e.error.status || e.error.status < 400 || e.error.status > 500) {
            console.error('MAP ERROR', e)
        }
    }

    // NO HOOKS BELOW HERE
    if (!baseStyle || !bounds) return <MapLoading />

    return (
        <div className={classes.mapContainer}>
            <MapGL
                {...viewport}
                width="100%"
                height="100%"
                minZoom={zoomBounds[0] || 4}
                maxZoom={zoomBounds[1] || 20}
                clickRadius={2}
                onLoad={loadHandler}
                onResize={handleResize}
                mapStyle={baseStyle}
                onViewportChange={setViewport}
                onError={onError}
                mapboxApiAccessToken={config.mapConfig.mapBoxToken}
            >
                <NavigationControl style={navControlStyle} />
                {mapLoader}
                {children}
            </MapGL>
        </div>
    )
}

export default Map
