import React from 'react'
import ReactDOM from 'react-dom'
import AppAuthConnect from './connectors/AppAuth.connect'
import { Router } from 'react-router-dom'
import { hashHistory } from '@riverscapes/react-common'
import log from 'loglevel'
// import { devSetup } from '@northarrowresearch/react-common'
// Here's our global overrides css
import './base.css'
import { RecoilRoot } from 'recoil'

// THIS FIX IS NECESSARY FOR PRODUCTION AND WEBPACK!!!!
// https://docs.mapbox.com/mapbox-gl-js/api/#transpiling-v2
// https://github.com/mapbox/mapbox-gl-js/issues/10173
import mapboxgl from 'mapbox-gl/dist/mapbox-gl'
// eslint-disable-next-line @typescript-eslint/no-var-requires
mapboxgl.workerClass = require('worker-loader!mapbox-gl/dist/mapbox-gl-csp-worker').default

// This does its own process.env.NODE_ENV check
// In production it only presents a minimal set of tools
// devSetup()
log.setDefaultLevel(log.levels.DEBUG)

// This is the font we need
require('typeface-roboto')

ReactDOM.render(
    <RecoilRoot>
        <Router basename="/" history={hashHistory}>
            <AppAuthConnect />
        </Router>
    </RecoilRoot>,
    document.getElementById('root')
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
// serviceWorker.register()

// On android this will trick the back button into not closing the app
window.addEventListener('load', () => {
    window.history.pushState({}, '')
})

// In Chrome > 52 we need to explicitly request persistent storage to make sure our
// stuff isn't cleaned up automatically.
if (navigator.storage && navigator.storage.persist) {
    navigator.storage.persist().then((granted) => {
        if (granted) {
            log.debug('Storage will not be cleared except by explicit user action')
        } else {
            log.warn('Storage may be cleared by the UA under storage pressure.')
        }
    })
} else {
    log.warn('navigator.storage API not implemented.')
}
