import React from 'react'
import { TilingStatus, ProjectTreeLayerTypes, ProjectTreeLayerTileTypes } from '@riverscapes/common'
// import log from 'loglevel'
import TreeItem from '@material-ui/lab/TreeItem'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'

import TreeNodeLabel from './TreeNodeLabel'

import { LayerIcon } from '@riverscapes/react-icons'
import clsx from 'clsx'
// import log from 'loglevel'

export interface TreeLeafProps {
    id: number
    layerType: ProjectTreeLayerTypes
    label: string
    tilesState: TilingStatus
    active?: boolean
    hidden?: boolean
    searchText?: string
    handleContext: (lid: number, mousex: number, mousey: number) => void
    handleClick: (lid: number) => void
    handleDoubleClick: (lid: number) => void
}
// const FADE_TIME = 500

const useLeafStyles = makeStyles((theme) => ({
    leaf: {
        cursor: 'context-menu',
        marginLeft: theme.spacing(2)
    },
    active: {
        fontWeight: 'bold'
    },
    treeLeafLabel: {
        display: 'flex'
    },
    innerLabel: {
        flexGrow: 0
    },
    spacer: {
        flexGrow: 1
    },
    metaIcon: {
        margin: theme.spacing(0.25),
        opacity: 0.2,
        fontSize: theme.spacing(2),
        lineHeight: '1rem'
    },
    loadingSpinner: {
        color: theme.palette.text.secondary
    },
    tileStatus: {
        flexGrow: 0,
        fontStyle: 'italic'
    },
    leafQUEUED: {
        color: '#999999',
        fontStyle: 'italic'
    },
    leafNO_GEOMETRIES: {
        color: '#666666'
    },
    leafCREATING: {
        color: 'orange'
    },
    leafTILING_ERROR: {
        color: 'red',
        fontStyle: 'italic'
    },
    leafTIMEOUT: {
        color: '#999999',
        fontStyle: 'italic'
    },
    leafFETCH_ERROR: {
        color: 'red',
        fontStyle: 'italic'
    },
    leafNOT_FOUND: {
        color: 'red',
        fontStyle: 'italic'
    },
    leafINDEX_NOT_FOUND: {
        color: 'red',
        fontStyle: 'italic'
    },
    leafLAYER_NOT_FOUND: {
        color: 'red',
        fontStyle: 'italic'
    },
    leafUNKNOWN: {
        color: theme.palette.text.secondary,
        fontStyle: 'italic'
    },
    leafFETCHING: {
        color: theme.palette.text.secondary,
        fontStyle: 'italic'
    },
    leafSUCCESS: {}
}))

export const TreeLeaf: React.FC<TreeLeafProps> = ({
    id,
    layerType,
    label,
    active,
    hidden,
    searchText,
    tilesState,
    handleContext,
    handleClick,
    handleDoubleClick
}: TreeLeafProps) => {
    const classes = useLeafStyles()

    const onCtxMenu = (event) => {
        event.preventDefault()
        handleContext(id, event.clientX - 2, event.clientY - 4)
    }
    let tileStatusStr: React.ReactElement | string | null = null
    if (ProjectTreeLayerTileTypes.indexOf(layerType) > -1 || layerType === ProjectTreeLayerTypes.REPORT) {
        switch (tilesState) {
            case TilingStatus.SUCCESS:
                // tileStatusStr = 'Available'
                break
            case TilingStatus.QUEUED:
                tileStatusStr = 'Queued...'
                break
            case TilingStatus.CREATING:
                tileStatusStr = 'Building...'
                break
            case TilingStatus.TILING_ERROR:
                tileStatusStr = 'Tile Error'
                break
            case TilingStatus.TIMEOUT:
                tileStatusStr = 'Timeout'
                break
            case TilingStatus.INDEX_NOT_FOUND:
                tileStatusStr = 'Index Error'
                break
            case TilingStatus.NO_GEOMETRIES:
                tileStatusStr = 'Empty'
                break
            case TilingStatus.LAYER_NOT_FOUND:
                tileStatusStr = 'Layer Missing'
                break
            case TilingStatus.FETCH_ERROR:
                tileStatusStr = 'Fetch Error'
                break
            case TilingStatus.UNKNOWN:
                tileStatusStr = (
                    <CircularProgress className={classes.loadingSpinner} color="inherit" thickness={3} size={15} />
                )
                break
            case TilingStatus.FETCHING:
                tileStatusStr = (
                    <CircularProgress className={classes.loadingSpinner} color="inherit" thickness={3} size={15} />
                )
                break
            default:
                break
        }
    }

    return (
        <TreeItem
            // this is IMPORTANT! 'l-##' denotes a branch
            nodeId={`l-${id}`}
            className={clsx(classes.leaf, tilesState && classes[`leaf${tilesState}`])}
            // TODO: Temproarily hide layers
            hidden={hidden}
            onClick={() => handleClick(id)}
            onDoubleClick={() => handleDoubleClick(id)}
            label={
                <div className={classes.treeLeafLabel}>
                    <Typography
                        variant="body2"
                        color="inherit"
                        className={clsx(classes.innerLabel, active && classes.active)}
                    >
                        <TreeNodeLabel text={label} query={searchText} />
                    </Typography>
                    <div className={classes.spacer} />
                    {tileStatusStr && (
                        <Typography
                            variant="caption"
                            color="inherit"
                            className={clsx(classes.innerLabel, active && classes.active)}
                        >
                            {tileStatusStr}
                        </Typography>
                    )}
                </div>
            }
            onContextMenu={onCtxMenu}
            icon={<LayerIcon layerType={layerType} />}
        />
    )
}

export default TreeLeaf
