import React, { useState } from 'react'
import IconButton from '@material-ui/core/IconButton'
import ListItem from '@material-ui/core/ListItem'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'
import CircularProgress from '@material-ui/core/CircularProgress'
import ColorRamp from './ColorRamp'
import CancelIcon from '@material-ui/icons/Cancel'
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'

import { LayerIcon } from '@riverscapes/react-icons'
import { ProjectTreeLayerTypes, Symbology } from '@riverscapes/common'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'

import { ListItemIcon, Color } from '@material-ui/core'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import clsx from 'clsx'

const useStyles = makeStyles((theme) => ({
    container: {},
    listItem: {
        paddingTop: 0,
        paddingBottom: 0,
        paddingLeft: 0,
        paddingRight: 26
    },
    listItemActive: {
        '& *': {
            cursor: 'pointer'
        }
    },
    listItemAvatar: {
        minWidth: theme.spacing(3.5)
    },
    expandIcon: {
        minWidth: theme.spacing(1)
    },
    avatar: {
        width: theme.spacing(3),
        height: theme.spacing(3),
        '& svg': {
            height: '0.7em',
            width: '0.7em'
        }
    },
    expandIconButton: {
        padding: 0,
        '& svg': {
            width: '0.7em',
            height: '0.7em'
        }
    },
    expandDisabled: {
        color: 'rgba(0,0,0,0.05)!important'
    },
    textPrimary: {
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis'
    },
    textSecondary: {
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis'
    },
    secondary: {
        right: 0
    },
    root: {
        marginTop: theme.spacing(2)
    },
    heading: {
        marginTop: theme.spacing(1)
    },
    loadingSpinner: {
        color: theme.palette.text.secondary
    },
    layerItem: {
        display: 'flex'
    },
    legendName: {
        flexGrow: 1
    },
    deleteButtonHover: {},
    legendColor: {
        border: '1px solid black',
        width: '20px',
        display: 'inline-block',
        height: '14px',
        marginRight: '5px'
    },
    deleteButton: {
        flexGrow: 0,
        color: '#CCCCCC',
        padding: 0,
        marginRight: 0,
        '& < svg': {
            width: '0.8em',
            height: '0.8em'
        }
    },
    zoomButton: {
        flexGrow: 0,
        color: '#CCCCCC',
        padding: 0,
        marginRight: 0,
        '& < svg': {
            width: '0.8em',
            height: '0.8em'
        }
    }
}))

export interface LegendLayerItemProps {
    label: string
    leafId: number
    layerType: ProjectTreeLayerTypes
    subLabel?: string
    overZoomed?: boolean
    max?: number
    min?: number
    fallbackColor?: Color
    loading?: boolean
    zoomLayerExtent?: () => void
    symbology?: Symbology | null
    removeLayer: (lid: number) => void
}

const LegendLayerItem: React.FC<LegendLayerItemProps> = ({
    label,
    leafId,
    loading,
    max,
    min,
    subLabel,
    fallbackColor,
    zoomLayerExtent,
    symbology,
    layerType,
    removeLayer
}: LegendLayerItemProps) => {
    const classes = useStyles()
    const theme = useTheme()
    const [open, setOpen] = useState<boolean>(true)

    const hasLegend = Boolean(symbology && symbology.legend && symbology.legend.length > 1)
    const hasOneItemLegend = Boolean(symbology && symbology.legend && symbology.legend.length === 1)

    let fgColor = theme.palette.text.primary
    let bgColor = theme.palette.background.default
    if (hasOneItemLegend && symbology?.legend) {
        if (layerType === ProjectTreeLayerTypes.POLYGON || layerType === ProjectTreeLayerTypes.RASTER) {
            bgColor = symbology.legend[0][0]
            fgColor = theme.palette.getContrastText(bgColor)
        } else {
            fgColor = symbology.legend[0][0]
            bgColor = theme.palette.background.default
        }
        // fgColor = symbology?.legend?[0][0]
    } else if (fallbackColor) {
        if (layerType === ProjectTreeLayerTypes.POLYGON || layerType === ProjectTreeLayerTypes.RASTER) {
            fgColor = fallbackColor[500]
            bgColor = fallbackColor[100]
        } else {
            fgColor = fallbackColor[500]
            bgColor = theme.palette.background.default
        }
    }

    const toggle = (): void => (hasLegend ? setOpen(!open) : undefined)

    return (
        <React.Fragment>
            <ListItem dense className={clsx(classes.listItem, hasLegend && classes.listItemActive)}>
                <ListItemIcon className={classes.expandIcon}>
                    <IconButton
                        size="small"
                        onClick={toggle}
                        disabled={!hasLegend}
                        classes={{ root: classes.expandIconButton, disabled: classes.expandDisabled }}
                    >
                        {hasLegend && open ? <ExpandMoreIcon /> : <ChevronRightIcon />}
                    </IconButton>
                </ListItemIcon>
                <ListItemAvatar className={classes.listItemAvatar}>
                    <Avatar className={classes.avatar} style={{ background: bgColor }} onClick={toggle}>
                        <LayerIcon
                            layerType={layerType}
                            iconProps={{
                                style: { color: fgColor }
                            }}
                        />
                    </Avatar>
                </ListItemAvatar>
                <Tooltip
                    enterDelay={1500}
                    title={
                        <React.Fragment>
                            <Typography color="inherit">{label}</Typography>
                            {subLabel && <Typography variant="caption">{subLabel}</Typography>}
                        </React.Fragment>
                    }
                >
                    <ListItemText
                        primary={label}
                        secondary={subLabel}
                        onClick={toggle}
                        classes={{ primary: classes.textPrimary, secondary: classes.textSecondary }}
                    />
                </Tooltip>
                <ListItemSecondaryAction className={classes.secondary}>
                    {loading && (
                        <CircularProgress className={classes.loadingSpinner} color="inherit" thickness={3} size={15} />
                    )}
                    {!loading && (
                        <>
                            {zoomLayerExtent && (
                                <Tooltip enterDelay={1500} title="Zoom layer extents">
                                    <IconButton
                                        edge="end"
                                        className={classes.zoomButton}
                                        aria-label="zoomExtent"
                                        onClick={(event) => {
                                            event.stopPropagation()
                                            zoomLayerExtent()
                                        }}
                                    >
                                        <ZoomOutMapIcon />
                                    </IconButton>
                                </Tooltip>
                            )}
                            <Tooltip enterDelay={1500} title="Remove map layer">
                                <IconButton
                                    edge="end"
                                    className={classes.deleteButton}
                                    aria-label="delete"
                                    onClick={(event) => {
                                        event.stopPropagation()
                                        removeLayer(leafId)
                                    }}
                                >
                                    <CancelIcon />
                                </IconButton>
                            </Tooltip>
                        </>
                    )}
                </ListItemSecondaryAction>
            </ListItem>
            {hasLegend && open && (
                <ColorRamp
                    max={max}
                    min={min}
                    rampType={symbology?.rampType}
                    legend={symbology?.legend as [string, string][]}
                />
            )}
        </React.Fragment>
    )
}

export default LegendLayerItem
