import React, { useState } from 'react'
import Chip from '@material-ui/core/Chip'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import { ViewportProps } from 'react-map-gl'
import clsx from 'clsx'
import WarningOutlineIcon from '@material-ui/icons/WarningOutlined'
import { ContentCopy } from '@riverscapes/react-icons'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
    root: {},
    normalBG: {
        color: theme.palette.primary.contrastText,
        background: theme.palette.primary.light,
        '&:hover,&.focus': {
            color: theme.palette.primary.contrastText,
            background: theme.palette.primary.dark
        }
    },
    '@keyframes overzoomFade': {
        '0%': {
            backgroundColor: theme.palette.warning.dark
        },
        '50%': {
            backgroundColor: theme.palette.warning.light
        },
        '100%': {
            backgroundColor: theme.palette.warning.dark
        }
    },
    normalButtons: {
        color: theme.palette.primary.contrastText,
        background: theme.palette.primary.dark
    },
    overZBG: {
        color: theme.palette.warning.contrastText,
        background: theme.palette.warning.main,
        '&:hover,&.focus': {
            color: theme.palette.warning.contrastText,
            background: theme.palette.warning.dark
        },
        animation: '$overzoomFade 2s infinite'
    },
    overZButtons: {
        color: theme.palette.warning.contrastText,
        background: theme.palette.warning.dark
    },
    labelIcon: {
        background: 'transparent',
        '&:hover,&.focus': {
            background: 'transparent'
        }
    },
    warnText: {
        color: theme.palette.warning.light
    },
    label: {
        display: 'flex'
    },
    chipIcon: {
        height: theme.spacing(2),
        width: theme.spacing(2),
        fontSize: theme.typography.caption.fontSize,
        padding: 0,
        '& svg': {
            fontSize: theme.typography.caption.fontSize
        }
    },
    coordsTxt: {
        marginRight: theme.spacing(1),
        marginLeft: theme.spacing(1)
    },
    avatar: {},
    expanded: {},
    helpIcon: { marginLeft: theme.spacing(1) },
    overZoomed: {
        color: theme.palette.warning.contrastText,
        background: theme.palette.warning.main
    }
}))

interface ZoomIndicatorProps {
    className?: string
    viewport: ViewportProps
    overZoomed?: boolean
    onHelpClick: () => void
    onCopyClick: () => void
}

const ZoomIndicator: React.FC<ZoomIndicatorProps> = ({
    className,
    viewport,
    overZoomed,
    onHelpClick,
    onCopyClick
}: ZoomIndicatorProps) => {
    const classes = useStyles()
    const [expanded, setExpanded] = useState<boolean>(false)
    const cc = {
        bg: overZoomed ? classes.overZBG : classes.normalBG,
        btn: overZoomed ? classes.overZButtons : classes.normalButtons
    }
    const preventDefault = (cb?: () => unknown) => (e) => {
        e.preventDefault()
        e.stopPropagation()
        if (cb) cb()
    }
    return (
        <Chip
            size="small"
            onClick={() => setExpanded(!expanded)}
            className={clsx(classes.root, cc.bg, className)}
            clickable={false}
            classes={{
                icon: clsx(cc.bg, classes.labelIcon),
                labelSmall: classes.label
            }}
            label={
                <>
                    <Tooltip
                        title={
                            <>
                                <div>{!expanded ? 'Map zoom (click to expand)' : 'Map zoom & coords'}</div>
                                {overZoomed && (
                                    <div className={classes.warnText}>
                                        Warning: Map Zoom exceeds raster tile resolution
                                    </div>
                                )}
                            </>
                        }
                    >
                        <span>
                            {expanded && `Zoom: `}
                            {viewport.zoom ? viewport.zoom.toFixed(1) : '?'}
                            {expanded && (
                                <span className={classes.coordsTxt}>
                                    {' Center: '}( {viewport.longitude?.toFixed(5)}, {viewport.latitude?.toFixed(5)} ){' '}
                                </span>
                            )}
                        </span>
                    </Tooltip>
                    {expanded && (
                        <Tooltip title="Copy map coordinates to clipboard">
                            <IconButton
                                onMouseOver={preventDefault()}
                                className={clsx(classes.chipIcon, cc.btn, classes.helpIcon)}
                                onClick={preventDefault(onCopyClick)}
                            >
                                <ContentCopy />
                            </IconButton>
                        </Tooltip>
                    )}
                    {overZoomed && (
                        <Tooltip title="Click to learn more about this warning.">
                            <IconButton
                                onMouseOver={preventDefault()}
                                className={clsx(classes.chipIcon, cc.btn, classes.helpIcon)}
                                onClick={preventDefault(onHelpClick)}
                            >
                                <WarningOutlineIcon />
                            </IconButton>
                        </Tooltip>
                    )}
                </>
            }
        />
    )
}

export default ZoomIndicator
