import React from 'react'
import { ProjectTreeView } from '@riverscapes/common'
import ViewList from './ViewList'
import BaseLayerPicker from './BaseLayerPicker'
import { List } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import LegendError from './LegendError'
import { BaseLayer } from '../../types'

const useStyles = makeStyles((theme) => ({
    root: {
        zIndex: 1000,
        padding: theme.spacing(0),
        border: '1px solid #EEEEEE',
        borderRadius: 5,
        position: 'absolute',
        background: 'white',
        width: 270,
        maxHeight: '90%',
        overflow: 'hidden',
        boxShadow: '3px 3px 3px #00000066',
        top: 15,
        right: 15,
        display: 'flex',
        flexDirection: 'column'
    },
    subheaders: {
        background: 'white'
    },
    legendList: {
        height: '100%',
        flex: '1 1',
        overflow: 'scroll'
    }
}))

export interface LegendProps {
    defaultView: string
    views: ProjectTreeView[]
    error?: string
    viewId: string | null
    setViewId: (viewId: string) => void
    baseLayer: BaseLayer
    baseLayers: BaseLayer[]
    setBaseLayer: (baselayer: BaseLayer) => void
    children: React.ReactElement[]
}

const Legend: React.FC<LegendProps> = ({
    views,
    defaultView,
    children,
    baseLayer,
    baseLayers,
    setBaseLayer,
    error,
    viewId,
    setViewId
}: LegendProps) => {
    const classes = useStyles()
    return (
        <div className={classes.root}>
            <BaseLayerPicker baseLayer={baseLayer} baseLayers={baseLayers} setBaseLayer={setBaseLayer} />
            <ViewList activeView={viewId} defaultView={defaultView} handleChange={setViewId} projectViews={views} />
            <List dense disablePadding className={classes.legendList}>
                {children}
            </List>
            <LegendError error={error} />
        </div>
    )
}

export default Legend
