import React from 'react'
import { useRecoilState } from 'recoil'
import { googleAnalytics } from '@riverscapes/react-common'
import { drawerOpenAtom } from '../recoil'
import TreeToggleBtn from '../components/TreeToggleBtn'
import useZoomExtents from '../hooks/useZoomExtents'
import { activeBoundsSelector } from '../recoil'
import { useRecoilValue } from 'recoil'
// import log from 'loglevel'

const TreeToggleConnect: React.FC = () => {
    const [drawerOpen, toggleDrawer] = useRecoilState(drawerOpenAtom)
    const zoomLayerExtent = useZoomExtents()
    const bounds = useRecoilValue(activeBoundsSelector)
    return (
        <TreeToggleBtn
            key="treeToggleBtn"
            drawerOpen={drawerOpen}
            bounds={bounds}
            zoomExtents={() => {
                if (bounds) zoomLayerExtent(bounds)
            }}
            toggleDrawer={() => {
                googleAnalytics.event('webrave', 'set_map_drawer', !drawerOpen ? 'closed' : 'open')
                toggleDrawer(!drawerOpen)
            }}
        />
    )
}

export default TreeToggleConnect
