import React from 'react'
import ListItem from '@material-ui/core/ListItem'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'
import { makeStyles } from '@material-ui/core/styles'
import { RampType } from '@riverscapes/common'
// import log from 'loglevel'

const useStyles = makeStyles((theme) => ({
    root: {
        flexDirection: 'column',
        paddingLeft: theme.spacing(5)
    },
    tooltip: {
        display: 'flex',
        '&>div': {
            marginRight: theme.spacing(1)
        }
    },
    row: {
        width: '100%',
        display: 'flex'
    },
    colorBox: {
        flexGrow: 0,
        flexShrink: 0,
        border: '1px solid #eaeaea',
        overflow: 'hidden',
        color: '#FFFFFF00',
        width: theme.spacing(2),
        height: theme.spacing(2)
    },
    value: {
        color: theme.palette.text.secondary,
        paddingLeft: theme.spacing(1),
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis'
    }
}))

export interface ViewListProps {
    max?: number
    min?: number
    rampType?: RampType
    legend: [string, string][]
}
const ViewList: React.FC<ViewListProps> = ({ max, min, rampType, legend }: ViewListProps) => {
    const classes = useStyles()
    const pRamp = parseRamp(legend, max, min, rampType)
    return (
        <ListItem divider dense disableGutters className={classes.root}>
            {pRamp.map((row, idr) => (
                <div key={`cr-${idr}`} className={classes.row}>
                    <div className={classes.colorBox} style={{ background: row[0] }}>
                        color
                    </div>
                    <Tooltip
                        enterDelay={2000}
                        title={
                            <div className={classes.tooltip}>
                                <div className={classes.colorBox} style={{ background: row[0] }}>
                                    color
                                </div>
                                {row[1]}
                            </div>
                        }
                    >
                        <Typography variant="caption" component="div" className={classes.value}>
                            {row[1]}
                        </Typography>
                    </Tooltip>
                </div>
            ))}
        </ListItem>
    )
}

export default ViewList

function parseRamp(ramp: [string, string][], max?: number, min?: number, rampType?: RampType): [string, string][] {
    let newRamp: [string, string][] = []
    if (!max || !min || rampType !== RampType.INTERPOLATED) newRamp = [...ramp]
    else {
        newRamp = ramp.map(([color, value]) => {
            const regMatch = value.match(/^([0-9.]+)%$/)
            if (regMatch) {
                const percent = parseFloat(regMatch[1])
                const interp = (min + ((max - min) * percent) / 100).toFixed(2)
                return [color, interp]
            } else return [color, value]
        })
    }
    const newRampDict = newRamp.reduce((acc, r) => ({ ...acc, [`${r[0]}${r[1]}`]: r }), {})
    return Object.values(newRampDict)
}
