import React, { useEffect } from 'react'
import { useRecoilCallback, useRecoilValueLoadable } from 'recoil'
import {
    tilesRequestIdAtomFamily,
    tilesRetriesAtomFamily,
    boundsAtom,
    zoomBoundsAtom,
    tilesSelectorFamily
} from '../recoil'
import { appDefaults } from '../config'
import { newBounds, newZoomBounds } from '../lib/util'
import { TileService, TilingStatus } from '@riverscapes/common'
// import log from 'loglevel'
import { isEqual } from 'lodash'

interface MPDProps {
    layerPath: string
}

const MapTilesConnect: React.FC<MPDProps> = ({ layerPath }: MPDProps) => {
    const tLoad = useRecoilValueLoadable(tilesSelectorFamily(layerPath))

    const updateBoundsState = useRecoilCallback(({ snapshot, set }) => async () => {
        const tLoad = snapshot.getLoadable(tilesSelectorFamily(layerPath))
        if (tLoad.state !== 'hasValue') return
        const tiles = tLoad.contents as TileService
        // Now we have to carefully decide whether to queue up another fetch or give up
        const reqId = await snapshot.getPromise(tilesRequestIdAtomFamily(layerPath))
        const retryId = await snapshot.getPromise(tilesRetriesAtomFamily(layerPath))
        const bounds = await snapshot.getPromise(boundsAtom)
        const zoomBounds = await snapshot.getPromise(zoomBoundsAtom)

        // If we failed to fetch then retry the network call
        if (tiles.status === TilingStatus.FETCH_ERROR) {
            if (retryId < 5)
                setTimeout(() => set(tilesRetriesAtomFamily(layerPath), retryId + 1), appDefaults.retryDelayS * 1000)
        }
        // If the tile status is on a transitory state (QUEUED, UNKNOWN, CREATING) then try again in (n) seconds
        else if (
            tiles.status === TilingStatus.QUEUED ||
            tiles.status === TilingStatus.UNKNOWN ||
            tiles.status === TilingStatus.CREATING
        ) {
            setTimeout(() => set(tilesRequestIdAtomFamily(layerPath), reqId + 1), appDefaults.refreshDelayS * 1000)
        } else if (tiles.status === TilingStatus.SUCCESS) {
            // Here's where the bounds gets calculated. The actual geofencing happens separately.
            const newBoundsCalc = newBounds(appDefaults.worldBounds, bounds, tiles.bounds || null)
            const newZoomBoundsCalc = newZoomBounds(zoomBounds, [tiles.minZoom, tiles.maxZoom])

            if (!isEqual(bounds, newBoundsCalc)) {
                set(boundsAtom, newBoundsCalc)
            }
            if (!isEqual(zoomBounds, newZoomBoundsCalc)) {
                set(zoomBoundsAtom, newZoomBoundsCalc)
            }
        }
        // Otherwise there's nothing left to do:
        // (SUCCESS, NOT_APPLICABLE, NO_GEOMETRIES, TILING_ERROR, INDEX_NOT_FOUND, TIMEOUT)
    })
    useEffect(() => {
        updateBoundsState()
    }, [tLoad])

    return null
}

export default MapTilesConnect
