import React from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import { dates } from '@northarrowresearch/common'
import { makeStyles } from '@material-ui/core/styles'
import { Typography, Link } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1)
    },
    table: {},
    section: {
        marginBottom: theme.spacing(3)
    },
    sectionHeader: {
        borderBottom: '1px solid'
    },
    metaKey: { fontWeight: 'bold' },
    metaValue: {
        color: theme.palette.text.secondary,
        overflowWrap: 'anywhere',
        textAlign: 'left'
    }
}))

interface MetaDataProps {
    title: string
    meta: Record<string, string>
}

const SPECIAL_FORMATS: Record<string, (val: string) => string> = {
    datecreated: (val) => {
        const valDate = dates.HTML5InputToTimestamp(val)
        return valDate > 0 ? `${dates.formatDate(valDate, 'lll')} (${dates.formatDateAgo(valDate)})` : 'unknown'
    }
}
const formatString = (key: string, val: string): React.ReactNode => {
    const lwrKey = key.toLowerCase()
    if (Object.keys(SPECIAL_FORMATS).indexOf(lwrKey) > -1) {
        return SPECIAL_FORMATS[lwrKey](val)
    } else if (
        val &&
        val.match(/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)/)
    ) {
        return (
            <Link href={val} target="_blank">
                {val}
            </Link>
        )
    } else return val
}

const MetaData: React.FC<MetaDataProps> = ({ title, meta }: MetaDataProps) => {
    const classes = useStyles()
    if (Object.keys(meta).length === 0) return null
    return (
        <div className={classes.section}>
            <Typography variant="subtitle1" className={classes.sectionHeader}>
                {title}
            </Typography>
            <TableContainer>
                <Table className={classes.table} size="small" aria-label="a dense table">
                    <TableBody>
                        {Object.keys(meta).map((rowKey) => (
                            <TableRow key={rowKey}>
                                <TableCell component="th" scope="row" className={classes.metaKey}>
                                    {rowKey}
                                </TableCell>
                                <TableCell align="right" className={classes.metaValue}>
                                    {formatString(rowKey, meta[rowKey])}
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}

export default MetaData
