import React from 'react'

import Paper from '@material-ui/core/Paper'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
import { WarehouseButton } from '@riverscapes/react-common'
import { ProjectTreeLeaf } from '@riverscapes/common'

import MetaData from './MetaData'

// TODO: Maybe this isn't the best but I don't want to pass it through the whole system
// Maybe a connector for WarehouseButton????
import { warehouseUrl } from '../../config'

const DONTSHOW = ['_rs_lyr_origin_url']

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(1)
    },
    minimal: {
        // background:
    },
    whNote: {
        padding: theme.spacing(2, 1)
    }
}))

interface LayerMetaProps {
    leaf: ProjectTreeLeaf
    minimal?: boolean
}

function makeSections(meta: Record<string, string>): Record<string, Record<string, string>> {
    const keys = Object.keys(meta).filter((k) => DONTSHOW.indexOf(k) < 0)

    const distill = (key) =>
        keys.filter((k) => k.indexOf(key) === 0).reduce((acc, rk) => ({ ...acc, [rk.replace(key, '')]: meta[rk] }), {})

    return {
        upstreamWarehouse: distill('_rs_wh_'),
        upstreamProject: distill('_rs_prj_'),
        upstreamLayer: distill('_rs_lyr_'),
        Layer: keys.filter((k) => k.indexOf('_rs') !== 0).reduce((acc, rk) => ({ ...acc, [rk]: meta[rk] }), {})
    }
}

const LayerMeta: React.FC<LayerMetaProps> = ({ leaf, minimal }: LayerMetaProps) => {
    const classes = useStyles()
    const meta = leaf.meta || {}
    const sections = makeSections(meta)
    return (
        <Paper className={minimal ? classes.minimal : classes.root}>
            {Object.keys(sections.Layer).length > 0 && <MetaData title="Layer Metadata" meta={sections.Layer} />}
            {!minimal && Object.keys(sections.upstreamWarehouse).length > 0 && (
                <div className={classes.whNote}>
                    <Typography variant="caption" component="div">
                        This layer was imported from a {meta._rs_prj_projType} project in the Riverscapes warehouse. You
                        can visit the original project using the button below.
                    </Typography>
                    <WarehouseButton
                        title={`Original ${meta._rs_prj_projType} Project`}
                        href={`${warehouseUrl}#/${meta['_rs_wh_program']}/${meta['_rs_wh_id']}`}
                    />
                </div>
            )}
            {Object.keys(sections.upstreamProject).length > 0 && (
                <MetaData title="Original Project Metadata" meta={sections.upstreamProject} />
            )}
            {Object.keys(sections.upstreamLayer).length > 0 && (
                <MetaData title="Original Layer Metadata" meta={sections.upstreamLayer} />
            )}
            <MetaData title="Other" meta={{ Symbology: leaf.symbology || 'None' }} />
        </Paper>
    )
}

export default LayerMeta
