import React from 'react'

import TreeItem from '@material-ui/lab/TreeItem'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import TreeNodeLabel from './TreeNodeLabel'
import clsx from 'clsx'

export interface TreeBranchProps {
    id: number
    label: string
    hidden?: boolean
    searchText?: string
    children?: React.ReactChild[]
}

const useBranchStyles = makeStyles((theme) => ({
    branch: {
        marginTop: theme.spacing(0.5)
    },
    iconContainer: {
        width: theme.spacing(4)
    },
    label: {
        // paddingLeft: theme.spacing(1.5)
    }
}))

export const TreeBranch: React.FC<TreeBranchProps> = ({ id, hidden, label, searchText, children }: TreeBranchProps) => {
    const classes = useBranchStyles()
    return (
        <TreeItem
            // this is IMPORTANT! 'b-##' denotes a branch
            nodeId={`b-${id}`}
            className={clsx(classes.branch)}
            classes={{
                label: classes.label,
                iconContainer: classes.iconContainer
            }}
            onDoubleClick={(e) => {
                e.preventDefault()
                e.stopPropagation()
            }}
            onContextMenu={(e) => {
                e.preventDefault()
                e.stopPropagation()
            }}
            hidden={hidden}
            label={
                <Typography variant="body1">
                    <TreeNodeLabel text={label} query={searchText} />
                </Typography>
            }
        >
            {children}
        </TreeItem>
    )
}

export default TreeBranch
