import React from 'react'

import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import { makeStyles } from '@material-ui/core/styles'

import { ProjectTreeLeaf, TileService } from '@riverscapes/common'
import { LayerIcon } from '@riverscapes/react-icons'

import LayerMeta from './LayerMeta'
import LayerTileInfo from './LayerTileInfo'

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100%',
        overflow: 'hidden',
        display: 'flex',
        flexDirection: 'column'
    },
    toolbar: {
        flex: '0 0 ',
        padding: theme.spacing(0, 1),
        minHeight: theme.spacing(6),
        background: theme.palette.primary.dark,
        color: theme.palette.primary.contrastText
    },
    spacer: { flexGrow: 1 },
    layerTitle: {},
    layerIcon: {},
    layerMeta: {
        flexGrow: 1,
        overflowY: 'scroll',
        userSelect: 'text',
        '& *': {
            userSelect: 'text'
        }
    },
    typeContainer: {
        // borderRadius: theme.spacing(1),
        // background: '#FFFFFF22',
        // margin: theme.spacing(1),
        // padding: theme.spacing(0, 1),
        // border: '1px solid #FFFFFF22'
    },
    closeButton: {
        // marginLeft: theme.spacing(2)
    }
}))

interface LayerInfoProps {
    leaf: ProjectTreeLeaf
    tiles?: TileService
    handleClose: () => void
}

const LayerInfo: React.FC<LayerInfoProps> = ({ leaf, tiles, handleClose }: LayerInfoProps) => {
    const classes = useStyles()
    return (
        <div className={classes.root}>
            <Toolbar className={classes.toolbar}>
                <Tooltip title={`This is a ${leaf.layerType} layer`}>
                    <IconButton edge="start" className={classes.layerIcon} color="inherit" aria-label="menu">
                        <LayerIcon layerType={leaf.layerType} />
                    </IconButton>
                </Tooltip>
                <Typography variant="body2" className={classes.layerTitle}>
                    {leaf.label}
                </Typography>
                <div className={classes.spacer} />
                {/* <div className={classes.typeContainer}>
                    </div> */}
                <IconButton
                    edge="start"
                    className={classes.closeButton}
                    color="inherit"
                    aria-label="menu"
                    onClick={handleClose}
                >
                    <CloseIcon />
                </IconButton>
            </Toolbar>
            <div className={classes.layerMeta}>
                {leaf && <LayerMeta leaf={leaf} />}
                {tiles && <LayerTileInfo tiles={tiles} />}
            </div>
        </div>
    )
}

export default LayerInfo
