import React from 'react'
import { Fab, Tooltip } from '@material-ui/core'
import LayersClearIcon from '@material-ui/icons/LayersClear'
import { makeStyles } from '@material-ui/core/styles'
import LayersIcon from '@material-ui/icons/Layers'
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'

interface Props {
    drawerOpen?: boolean
    toggleDrawer: () => void
    bounds: [number, number, number, number] | null
    zoomExtents: () => void
}

const useStyles = makeStyles((theme) => ({
    toggleTree: {
        position: 'absolute',
        top: theme.spacing(1),
        left: theme.spacing(1)
    },
    zoomExtent: {
        position: 'absolute',
        top: theme.spacing(1),
        left: theme.spacing(7)
    }
}))

const ProjectLoading: React.FC<Props> = ({ drawerOpen, toggleDrawer, zoomExtents, bounds }: Props) => {
    const classes = useStyles()
    return (
        <>
            <Tooltip title="Toggle the tree sidebar">
                <Fab
                    color="primary"
                    aria-label="add"
                    onClick={toggleDrawer}
                    size="small"
                    className={classes.toggleTree}
                >
                    {drawerOpen ? <LayersClearIcon /> : <LayersIcon />}
                </Fab>
            </Tooltip>
            <Tooltip
                title={!bounds ? 'Cannot zoom to map extent. No bounding box found' : 'Zoom to active layer extent'}
            >
                <Fab
                    color="secondary"
                    aria-label="zoomOut"
                    onClick={() => zoomExtents()}
                    size="small"
                    disabled={!bounds}
                    className={classes.zoomExtent}
                >
                    <ZoomOutMapIcon />
                </Fab>
            </Tooltip>
        </>
    )
}

export default ProjectLoading
