import { useCallback, useEffect } from 'react'
import { useRecoilCallback, useRecoilValue, useSetRecoilState, useRecoilState } from 'recoil'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import useViews from './useViews'
import {
    authAtom,
    projectAtom,
    activeIdsAtom,
    tilesSelectorFamily,
    symbologySelectorFamily,
    projectLayerPathsSelector,
    projectSymbologiesSelector,
    treeExpandedIdsAtom,
    SymbologyKey
} from '../recoil'
import { Project } from '@riverscapes/common'
import log from 'loglevel'

/**
 * This custom hook does all the state setup we need to initialize the project once it is loaded
 */
const useAppStateInit = (initAuth?: UseHostedReturn, initProj?: Project): void => {
    const [project, setProjectTree] = useRecoilState(projectAtom)
    const setActiveIds = useSetRecoilState(activeIdsAtom)
    const setAuth = useSetRecoilState(authAtom)
    const projectSymbologies = useRecoilValue(projectSymbologiesSelector)
    const projectLayerPaths = useRecoilValue(projectLayerPathsSelector)

    const setExpanded = useSetRecoilState(treeExpandedIdsAtom)
    const { resetView } = useViews()

    const resetApp = useCallback(() => {
        log.debug('RESETTING THE APP')
    }, [])

    // This just basically stuffs our project into Recoil
    // TODO: This may not be needed later when the common library uses recoil too
    useEffect(() => {
        if ((!initProj && project) || (initProj && project && initProj.id !== project.id)) {
            resetApp()
        }
        setProjectTree(initProj || null)
    }, [initProj])
    useEffect(() => {
        // if (!initAuth || initAuth.isAuthenticated) resetApp()
        setAuth(initAuth || null)
    }, [initAuth])
    // If the project or the auth changes we reset everything

    const preFetchLayerTile = useRecoilCallback(({ snapshot }) => (layerPath: string) => {
        snapshot.getLoadable(tilesSelectorFamily(layerPath)) // pre-fetch user info
    })

    const preFetchSymbology = useRecoilCallback(({ snapshot }) => (sKey: SymbologyKey) => {
        snapshot.getLoadable(symbologySelectorFamily(sKey)) // pre-fetch symbology
    })

    // TODO:
    // - load url params

    // Here we set the default expanded nodes when the project loads
    useEffect(() => {
        if (!project) setExpanded([])
        else {
            setActiveIds([])
            resetView() // reset to the default view
            setExpanded(project.tree.branches.filter((br) => !br.collapsed).map((br) => `b-${br.bid}`))
        }
    }, [project])

    // When the symbologies change, preload them
    useEffect(() => {
        projectSymbologies.map(preFetchSymbology)
    }, [projectSymbologies])
    // When the unique layer paths change preload them
    useEffect(() => {
        projectLayerPaths.map(preFetchLayerTile)
    }, [projectLayerPaths])
}

export default useAppStateInit
