import React, { useState } from 'react'

import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        overflow: 'hidden'
    },
    appBar: {
        flex: '0 0'
    },
    tabs: {},
    tabContents: {
        position: 'relative',
        overflow: 'hidden',
        flexGrow: 1
    },
    closeButton: {
        zIndex: 1000,
        background: theme.palette.secondary.main,
        color: theme.palette.secondary.contrastText,
        position: 'absolute',
        top: 0,
        right: 0
    }
}))

interface TreeDrawerProps {
    tabs: { label: string; value: React.ReactChild }[]
}

const TreeDrawer: React.FC<TreeDrawerProps> = ({ tabs }: TreeDrawerProps) => {
    const classes = useStyles()
    const [activeTab, setValue] = useState<number>(0)

    const handleChange = (event: React.ChangeEvent<Record<string, unknown>>, newValue: number) => {
        setValue(newValue)
    }
    return (
        <div className={classes.root}>
            <Tabs
                value={activeTab}
                className={classes.tabs}
                onChange={handleChange}
                indicatorColor="primary"
                textColor="primary"
                variant="fullWidth"
                aria-label="full width tabs example"
            >
                {tabs.map((tab, idx) => (
                    <Tab key={`tab-${idx}`} label={tab.label} value={idx} />
                ))}
            </Tabs>
            {tabs.map(({ value }, tid) => (
                <div
                    key={`tab-${tid}`}
                    className={classes.tabContents}
                    style={{ display: tid === activeTab ? 'block' : 'none' }}
                >
                    {value}
                </div>
            ))}
        </div>
    )
}

export default TreeDrawer
