import React from 'react'
import TreeContextMenu, { CtxMenuItemType } from '../components/tree/TreeContextMenu'
import { useRecoilValue, useSetRecoilState } from 'recoil'
import { layerStateSelectorFamily, TreeCtxState, treeInfoPaneIdAtom, treeInfoPaneOpenAtom } from '../recoil'
import { ProjectTreeLayerTypes, SymbologyStatus, TilingStatus } from '@riverscapes/common'
import useMapLayers from '../hooks/useMapLayers'
import useZoomExtents from '../hooks/useZoomExtents'

// Icons for our menu items
import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'
import AddCircleIcon from '@material-ui/icons/AddCircle'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import DownloadIcon from '@material-ui/icons/CloudDownload'
import HourglassEmptyIcon from '@material-ui/icons/HourglassEmpty'
import AssessmentIcon from '@material-ui/icons/Assessment'
import ErrorIcon from '@material-ui/icons/Error'
import InfoIcon from '@material-ui/icons/Info'

interface Props {
    ctxState: TreeCtxState
    downloadFile: (fkey: string) => Promise<void>
    onClose: () => void
}

const TreeContextMenuConnect: React.FC<Props> = ({ ctxState, onClose, downloadFile }: Props) => {
    const layer = useRecoilValue(layerStateSelectorFamily(ctxState.leafId))
    const setTreeInfoPaneId = useSetRecoilState(treeInfoPaneIdAtom)
    const setTreeeInfoPaneOpen = useSetRecoilState(treeInfoPaneOpenAtom)
    const { addLayer, removeLayer } = useMapLayers()
    const zoomLayerExtent = useZoomExtents()
    const menuItems: CtxMenuItemType = []

    if (layer.leaf.layerType === ProjectTreeLayerTypes.REPORT) {
        if (layer.tileStatus === TilingStatus.FETCHING || layer.tileStatus === TilingStatus.QUEUED) {
            menuItems.push({
                label: 'Report queued for upload...',
                Icon: HourglassEmptyIcon,
                disabled: true
            })
        } else {
            menuItems.push({
                label: 'Open Report',
                Icon: AssessmentIcon,
                disabled: !(layer.tiles?.url && layer.tileStatus === TilingStatus.SUCCESS),
                handleClick: (): void => {
                    const newWindow = window.open(layer.tiles?.url + 'index.html', '_blank', 'noopener,noreferrer')
                    if (newWindow) newWindow.opener = null
                    onClose()
                }
            })
        }
    } else if (layer.leaf.layerType === ProjectTreeLayerTypes.FILE) {
        menuItems.push({
            label: 'Download file',
            Icon: DownloadIcon,
            handleClick: (): void => {
                if (layer.leaf.layerPath) downloadFile(layer.leaf.layerPath)
                onClose()
            }
        })
    } else if (layer.tileStatus === TilingStatus.FETCHING || layer.symbologyStatus === SymbologyStatus.FETCHING) {
        menuItems.push({
            label: 'Loading tile data...',
            Icon: HourglassEmptyIcon,
            disabled: true
        })
    } else {
        // Now we add some layers
        if (layer.tileStatus === TilingStatus.SUCCESS) {
            if (layer.legendActive) {
                menuItems.push(
                    {
                        label: 'Remove from map',
                        Icon: RemoveCircleIcon,
                        handleClick: (): void => {
                            removeLayer(layer.leaf.id)
                            onClose()
                        }
                    },
                    {
                        label: 'Zoom layer extents',
                        Icon: ZoomOutMapIcon,
                        // disabled: Boolean(layer.tiles?.bounds),
                        handleClick: (): void => {
                            zoomLayerExtent(layer.tiles?.bounds)
                            onClose()
                        }
                    }
                )
            } else if (!layer.legendActive && layer.renderable) {
                menuItems.push(
                    {
                        label: 'Add to map',
                        Icon: AddCircleIcon,
                        handleClick: (): void => {
                            addLayer(layer.leaf.id)
                            onClose()
                        }
                    },
                    {
                        label: 'Zoom layer extents (add to map first)',
                        Icon: ZoomOutMapIcon,
                        disabled: true
                    }
                )
            }
        } else if (layer.tileStatus === TilingStatus.QUEUED) {
            menuItems.push({
                label: 'Queued for tiling...',
                Icon: HourglassEmptyIcon,
                disabled: true
            })
        } else if (layer.tileStatus === TilingStatus.NOT_APPLICABLE) {
            menuItems.push({
                label: 'Tiling is not applicable to this layer',
                Icon: ErrorIcon,
                disabled: true
            })
        } else if (layer.tileStatus === TilingStatus.NO_GEOMETRIES) {
            menuItems.push({
                label: 'No Geometries were found',
                Icon: ErrorIcon,
                disabled: true
            })
        } else if (layer.tileStatus === TilingStatus.CREATING) {
            menuItems.push({
                label: 'Currently building tiles...',
                Icon: HourglassEmptyIcon,
                disabled: true
            })
        } else if (layer.tileStatus === TilingStatus.TILING_ERROR) {
            menuItems.push({
                label: 'Tiles did not build',
                Icon: ErrorIcon,
                disabled: true,
                error: true
            })
        } else if (layer.tileStatus === TilingStatus.TIMEOUT) {
            menuItems.push({
                label: 'Tiling timed out',
                Icon: ErrorIcon,
                disabled: true,
                error: true
            })
        } else if (layer.tileStatus === TilingStatus.INDEX_NOT_FOUND) {
            menuItems.push({
                label: 'Error fetching tile info',
                Icon: ErrorIcon,
                disabled: true,
                error: true
            })
        } else if (layer.tileStatus === TilingStatus.LAYER_NOT_FOUND) {
            menuItems.push({
                label: 'The original GIS layer is missing',
                Icon: ErrorIcon,
                disabled: true,
                error: true
            })
        } else if (layer.tileStatus === TilingStatus.FETCH_ERROR) {
            menuItems.push({
                label: 'Error fetching tile info',
                Icon: ErrorIcon,
                disabled: true,
                error: true
            })
        }
        menuItems.push({
            label: 'Layer Information',
            Icon: InfoIcon,
            handleClick: (): void => {
                setTreeInfoPaneId(ctxState.leafId)
                setTreeeInfoPaneOpen(true)
                onClose()
            }
        })
    }

    return (
        <TreeContextMenu
            open={Boolean(ctxState)}
            position={ctxState.position}
            onClose={onClose}
            menuItems={menuItems}
        />
    )
}

export default TreeContextMenuConnect
