import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import { SvgIconComponent } from '@material-ui/icons'
import clsx from 'clsx'

const useStyles = makeStyles((theme) => ({
    root: {},
    item: {},
    menuItemIcon: {
        marginRight: theme.spacing(1)
    },
    error: {
        color: theme.palette.error.main
    }
}))

export type CtxMenuItemType = {
    label: string
    error?: boolean
    Icon?: SvgIconComponent
    handleClick?: () => void
    disabled?: boolean
}[]

export interface TreeControlProps {
    position?: { top: number; left: number }
    open: boolean
    onClose: () => void
    menuItems: CtxMenuItemType
}

const TreeContextMenu: React.FC<TreeControlProps> = ({ open, position, onClose, menuItems }: TreeControlProps) => {
    const classes = useStyles()
    return (
        <Menu
            keepMounted
            open={open}
            onClose={onClose}
            anchorReference="anchorPosition"
            anchorPosition={position}
            className={classes.root}
        >
            {menuItems.map(({ label, Icon, handleClick, disabled, error }, idx) => (
                <MenuItem
                    key={`ctxMenuIt-${idx}`}
                    onClick={handleClick}
                    disabled={disabled}
                    className={clsx(classes.item, error && classes.error)}
                >
                    {Icon && <Icon className={classes.menuItemIcon} color="inherit" />}
                    {label}
                </MenuItem>
            ))}
        </Menu>
    )
}

export default TreeContextMenu
