import React from 'react'

import TreeView from '@material-ui/lab/TreeView'
import { makeStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import InputAdornment from '@material-ui/core/InputAdornment'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'
import FolderIcon from '@material-ui/icons/Folder'
import FolderOpenIcon from '@material-ui/icons/FolderOpen'
import SearchIcon from '@material-ui/icons/Search'

import AppSplitter from '../AppSplitter'
import { debounce } from 'lodash'
// import log from 'loglevel'

const useStyles = makeStyles((theme) => ({
    container: {
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'column'
    },
    folderIcon: {
        color: theme.palette.secondary.main
    },
    tree: {
        flexGrow: 1,
        overflow: 'scroll',
        paddingBottom: theme.spacing(5)
    },
    searchBox: {
        flexGrow: 0,
        padding: theme.spacing(0.5)
    },
    searchBoxInput: {
        padding: theme.spacing(0.75, 0.5)
    }
}))

interface TreeControlProps {
    lyrInfoOpen: boolean
    setSearchText: (text: string) => void
    expanded: string[]
    ctxMenu: React.ReactElement | null
    lyrInfo: React.ReactElement | null
    selected: string[]
    setExpanded: (ids: string[]) => void
    setSelected: (id: string[]) => void
    children: React.ReactChild | React.ReactChild[]
}

const TreeControl: React.FC<TreeControlProps> = ({
    lyrInfoOpen,
    setSearchText,
    expanded,
    ctxMenu,
    lyrInfo,
    selected,
    setExpanded,
    setSelected,
    children
}: TreeControlProps) => {
    const classes = useStyles()

    const onNodeToggle = (event: React.ChangeEvent<Record<string, unknown>>, nodeIds: string[]) => {
        setExpanded(nodeIds)
    }

    const onNodeSelect = (event: React.ChangeEvent<Record<string, unknown>>, nodeIds: string | string[]) => {
        setSelected(Array.isArray(nodeIds) ? nodeIds : [nodeIds])
    }
    const setSearchDebounced = debounce((stext: string) => setSearchText(stext), 100)

    return (
        <AppSplitter
            open={lyrInfoOpen}
            split="horizontal"
            primary="first"
            defaultSize="60%"
            maxSize="80%"
            minSize="30%"
            child1={
                <div className={classes.container}>
                    <TextField
                        fullWidth
                        className={classes.searchBox}
                        placeholder="Filter Project Layers..."
                        variant="outlined"
                        onChange={(event) => {
                            setSearchDebounced((event.target.value as string).toLowerCase().trim())
                        }}
                        InputProps={{
                            classes: { input: classes.searchBoxInput },
                            startAdornment: (
                                <InputAdornment position="start">
                                    <SearchIcon />
                                </InputAdornment>
                            )
                        }}
                    />
                    <TreeView
                        className={classes.tree}
                        defaultCollapseIcon={
                            <>
                                <ExpandMoreIcon />
                                <FolderOpenIcon className={classes.folderIcon} />
                            </>
                        }
                        defaultExpandIcon={
                            <>
                                <ChevronRightIcon />
                                <FolderIcon className={classes.folderIcon} />
                            </>
                        }
                        onNodeToggle={onNodeToggle}
                        onNodeSelect={onNodeSelect}
                        selected={selected}
                        expanded={expanded}
                    >
                        {children}
                    </TreeView>
                    {ctxMenu}
                </div>
            }
            child2={lyrInfo || <div />}
        />
    )
}

export default TreeControl
